
.rjcv-tab {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.3rem;
  line-height: 1.5;
  text-align: center;
  word-break: break-all;
}
.rjcv-tab.rjcv-tab-sticky {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}
.rjcv-tab.rjcv-tab-scroll {
    overflow-x: scroll;
    -webkit-overflow-scrolling: touch;
    overscroll-behavior-x: none;
}
.rjcv-tab.rjcv-tab-scroll .rjcv-tabitem {
      -webkit-box-flex: 0;
      -webkit-flex: none;
          -ms-flex: none;
              flex: none;
}
